    font-family: 'Open Sans';
    src: url('OpenSans-Regular.woff2') format('woff2'), url('OpenSans-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Open Sans';
    src: url('OpenSans-SemiBold.woff2') format('woff2'), url('OpenSans-SemiBold.woff') format('woff');
    font-weight: 600;
